html, body {

}


button{
  border-width: 0;
  background-color: white;
  margin-right:43px;
}

form {
  border: 1px solid #d3d3d300;
  border-radius: 0;
}


.hide-price {
  display: none;
}


#list {
  margin: 2em 2em 2em 2em;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#root {
  overflow: no-display;
  min-height: 100vh;
  margin-top: 20px;
  list-style-type: none;
}

.homeContainerClosed {
  display: flex;
  flex-direction: column;
  position:relative;
}

.homeContainerOpen {
  display: flex;
  flex-direction: column;
  position:fixed;
}



#root > div > div > ul > li:nth-child {
  width: 15vh;
}

header > nav > div{
  display: flex;
  justify-content: space-evenly;
}


ul li{
  list-style-type: none;
}


#root > div {
  display: flex;
  flex-direction: column;
  position:relative;
}



.text-center {
  text-align: center;
}

.flex-wrapper {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.main-content {
  flex: 1;
  margin: 40px 0 15px;
}

.error-box {
  margin: 15px 0 !important;
}
.error-box i.circular.icon {
  box-shadow: 0 0 0 0.1em rgb(218, 41, 41) inset;
}

.clickable {
  cursor: pointer;
  /* color: #4183c4; */
  color: rgb(117, 117, 117);
}

.clickable:hover {
  /* color: #1e70bf; */
  color: rgb(71, 71, 71);
}

button.my-custom-button {
  cursor: pointer;
  border: 0;
  background: transparent;
}

button.my-custom-button:focus {
  color: #1e70bf;
  outline: 0 !important;
}

button.my-custom-button:hover {
  color: #1e70bf;
}

.ui.cards {
  flex-direction: column;
}

.ui.cards>.card {
  flex-direction: row;
}

.ui.cards>.card>.buttons.edit-buttons-on-card {
  margin: 5px;
  width: initial;
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
}

.ui.cards>.card>.image {
  width: 25%;
}


.ui.dropdown>.dropdown.icon {
  position: absolute;
  top: 14px;
  right: -8px;
}

.ui.toggle.checkbox .box:after, .ui.toggle.checkbox label:after {
  border: 1px solid #acacac;
}

.ui.toggle.checkbox .box:before, .ui.toggle.checkbox label:before {
  background: rgb(172, 172, 172);
}

@media (max-width: 600px) {
  .nav-logo { display: none !important; }
}

@media only screen and (min-width: 1200px) {
  .main-menu.menu .ui.container {
    width: calc(1127px + 2rem);
  }
}

@media only screen and (max-width: 1199px) and (min-width: 992px) {
  .main-menu.menu .ui.container {
    width: calc(933px + 2rem);
  }
}

.ui.menu .item>a:not(.ui) {
  color: #000000de;
}

.ui.secondary.main-menu.menu {
  position:fixed;
  width: 100%;
  background-color: rgba(255,255,255,0.85);
  z-index: 100;
}

body {
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Helvetica Neue",Arial,sans-serif;
}

@media only screen and (max-width: 767px) {
  .ui.container.head_banner {
    margin-left: 0px !important;
    margin-right: 0px !important;
  }
}

.head_banner .title_block {
  position: absolute;
  top:25%;
  background: rgba(68, 68, 68, 0.65);
  padding: 30px 55px !important
}

.head_banner .ui.header .title_block.header,
.head_banner .title_block.header,
.head_banner .ui.header.title_block .sub.header,
.head_banner.title_block .sub.header {
  color: #FFF;
}







